*{
	box-sizing:border-box;
	outline:none;
}

html{
  width:100%;
  height:100%;
}

body {
  margin: 0;
  padding: 0;
  width:100%;
  height:100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
	box-sizing:border-box;
}

.header_wrapper{
	width:100%;
	min-width:360px;
}
.header_container{
	position:relative;
	display:flex;
	justify-content:space-between;
	padding:15px;
	width:inherit;
	max-width:1600px;
	min-width:360px;
	margin:0 auto;
}

.header_container h1{
	font-weight:800;
	margin-top:10px;
}

.countryBtn{
	margin-top:10px;
}

.glyphicon-shopping-cart{
	font-size:24px;
	color:blue;
	display:none;
	padding-right:5px;
}

.addCartDiv{
	width:150px;
	height:50px;
	margin-top:10px;	
	border:2px solid #000;
	text-align:center;
	padding-top:5px;
}

.addCartBtn{
	border:0;
	background-color:#fff;
	font-size:1.4em;
	font-weight:500;
	padding-left:10px;
}

.cartValue{
	font-size:1.2em;
	font-weight:500;
}

@media screen and (max-width:800px){
	.header_container{
		padding:8px 0;
	}
	.glyphicon-shopping-cart{
		display:inline-block;
	}
	.countryBtn{
		position:absolute;
		right:80px;
		margin-top:15px;
	}
	.cartName{

		display:none;
	}
	.addCartDiv{
		width:80px;
		height:100%;
		border:0;
		padding:0;
		margin-top:15px;

	}
}

.nav_wrapper{
	padding:20px initial;
	border-top:2px solid #000;
	border-bottom:2px solid #000;
	width:100%;
	min-width:360px;
	padding:10px 0;
	position:relative;
}

.nav_container{
	width:inherit;
	max-width:1600px;
	min-width:360px;
	margin:0 auto;
	display:flex;
	justify-content:space-between;

}

.nav_list{
	list-style-type:none;
	display:flex;
	justify-content:center;
	align-items:center;
	margin:0;
}

.nav_list a{
	
	width:150px;
	text-decoration:none;
	color:#000;
	font-weight:500;
	font-size:1.4em;
	padding:5px 10px;
	text-align:center;

}

.sideNavBar{
	position:absolute;
	background-color:lightblue;
	width:250px;
	height:550px;
	padding-top:10px;
	background-color:#0F5A6E;
	color:#fff;
	top:0;
	left:0;
	z-index:5;
}

.navCloseBtn{
	width:80px;
	height:25px;
	border:none;
	background-color:transparent;
	border-radius:25px;
	font-size:1.2em;
	font-weight:700;
}

.sideNavBar .nav_list{
	width:inherit;
	height:inherit;
	display:flex;
	flex-direction:column;
	padding:0;

}

.sideNavBar .nav_list a{
	padding:20px 0;
	width:100%;
	color:#fff;
}

.sideNavBar .nav_list a:hover{
	background-color:#BDD7DE;
	color:#000;
}


@media screen and (max-width:860px){
	.nav_list{
		display:none;
	}
}
.content_wrapper{
	width:100%;
	min-width:360px;
	padding:20px 0;
}

.content_container{
	width:100%;
	max-width:1600px;
	min-width:360px;
	margin:0 auto;
	display:flex;
	/*border:2px solid blue;*/
	flex-wrap:wrap;
	justify-content:flex-start;
}

.imgDisplay_container{
	width:55%;
	/*padding:20px 0;*/

}

.imgTitle{
		padding:10px;
}

.imgTitle{
		display:none;
	}

.main_img{
	
	width:100%;
	min-width:360px;
	height:900px;
	margin-bottom:20px;
	text-align:center;
}

.main_img img{
	width:99%;
	height:99%;

}
.other_imgs{
	display:flex;
	justify-content:center;
	width:100%;
	min-width:360px;
}

.other_imgs div{
	display:block;
	width:202px;
	height:220px;
	margin-right:20px;
	text-align:center;
}
.other_imgs div:last-child{
	margin-right:0;
}

.other_imgs img{
	width:100%;
	height:100%;

}

.imgInfo_container{
	width:45%;
	padding-left:30px;
	font-size:1.2em;
	padding-top:10px;
	/*border:1px solid red;*/
}

.imgInfo_container ul:nth-child(3){
	list-style-type:none;
	padding:10px 0;
}

.imgInfo_container ul:nth-child(5){
	list-style-type:disc;
	padding-left:18px;
	margin-bottom:40px;
}

.imgInfo_container li{
	margin:5px;
}

.colorOptions,
.sizeOptions{
	display:inline-block;
	width:250px;
}

.colorOptions label,
.sizeOptions label{
	width:200px;
	font-size:1.2em;
	margin-bottom:15px;
}

.colorOptions select,
.sizeOptions select{
	width:200px;
	height:50px;
	margin-bottom:15px;
	font-size:1.2em;
}

.numberInput{
	display:inline-block;

}

.numberInput label{
	width:100%;
	min-width:360px;
	margin-bottom:15px;
	font-size:1.2em;
}

.numberInputBtn{
	width:80px;
	height:50px;
	text-align:center;
	font-size:1.2em;
}

input[type=number]::-webkit-inner-spin-button{
	width:50px;
	height:40px;
	border:5px solid black;
	opacity:1;

}

.clearInput{
	display:block;
	margin:40px 0;
	background-color:#fff;
	text-decoration:underline;
	border:0;
	font-weight:700;
}

/*this is Button*/
.addToCart,
.addToWishlist
{	
	display:inline-block;
	width:300px;
	height:80px;
	border:0;
	padding:10px;
	margin:15px 0;
	font-size:1.2em;
	font-weight:500;

}

.addToCart{
	background-color:#000;
	color:#fff;
	margin-right:30px;
}

.addToWishlist{
	background-color:#fff;
	color:#000;
	border:2px solid #000;
}

.addToCart:hover{
	background-color:darkblue;
}

.addToWishlist:hover{
	background-color:#ddd;
}

.socialMedia{
	display:flex;
	justify-content:space-between;
	align-items:center;
	width:100%;
	padding:40px 0;
	margin-top:50px;
	border-top:2px solid rgba(0,0,0,0.4);
	border-bottom:2px solid rgba(0,0,0,0.4);
}

.socialMediaTitle h4{
	font-size:1em;
	font-weight:700;
}

.socialMediaIcon{
	display:flex;
	flex-wrap:wrap;
	justify-content:space-around;
	align-items:center;
	width:80%;
	height:100%;

}

.fblikeBtn,
.fbshareBtn,
.twBtn,
.pinitBtn{
	width:80px;
	height:30px;
	border-radius:5px;
	margin-top:5px;
}

.fbBtn{
	display:flex;

}

.fblikeBtn,
.fbshareBtn{
	background-image:-webkit-linear-gradient(
		bottom,#0D0D90,#2424AA,#4444C2,#7272DB);
	background-image:linear-gradient(
		to top,#0D0D90,#2424AA,#4444C2,#7272DB);
	color:#fff;
	border:0;
}

.fa-facebook{
	margin-right:5px;
}

.fblikeBtn{
	font-size:1em;
	margin-right:10px;

}

.twBtn{
	background-image:-webkit-linear-gradient(
		bottom,#B8B8BE,#D4D4D8,#E7E7EB,#F9F9FC);
	background-image:linear-gradient(
		to top,#B8B8BE,#D4D4D8,#E7E7EB,#F9F9FC);
	border:1px solid #000;
}

.fa-twitter{
	font-size:1em;
	color:#0AB5DD;
}

.pinitBtn{
	color:#E60606;
	font-weight:500;
	border:1px solid #000;
	background-image:-webkit-linear-gradient(
		bottom,#B8B8BE,#D4D4D8,#E7E7EB,#F9F9FC);
	background-image:linear-gradient(
		to top,#B8B8BE,#D4D4D8,#E7E7EB,#F9F9FC);
}

.fa-pinterest-p{
	font-size:1em;
	color:#E60606;
}

.showLike{
	position:relative;
	border:1px solid #000;
	padding:2px 5px 2px 10px;
	margin-left:8px;
	height:30px;
	
}

.showLike:before{
	content:"";
	position:absolute;
	width:15px;
	height:15px;
	top:50%;
	left:0;
	background-color:#fff;
	transform:translate(-50%,-50%) rotateZ(45deg);
	-webkit-transform:translate(-50%,-50%) rotateZ(45deg);
	-moz-transform:translate(-50%,-50%) rotateZ(45deg);
	border-left:1px solid #000;
	border-bottom:1px solid #000;
	z-index:5;
}

.pinlike{
	border-left:0;
}

.pinlike:before{
	width:20px;
	height:20px;
	z-index:-1;
}

@media screen and (max-width:860px){
	.imgInfo_container,
	.imgDisplay_container{
		width:100%;
	}

	.imgTitle{
		display:block;
	}

	.imgInfoTitle{
		display:none;
	}

	.colorOptions,
	.sizeOptions{
		width:100%;
	}

	.colorOptions select,
	.sizeOptions select{
		width:80%;
	}
}

@media screen and (max-width:500px){
	.main_img{
		height:500px;
	}
	.other_imgs div{
		height:120px;
		margin-right:10px;
	}
	.imgInfo_container{
		margin-top:20px;
		padding-left:10px;
	}
	.addToCart{
		width:90%;
		margin-right:0;
		margin-left:10px;
	}
	.addToWishlist{
		width:90%;
		margin-left:10px;
	}

	input[type=number]::-webkit-inner-spin-button{
		width:20px;
		height:40px;
		border:5px solid black;
		opacity:1;
	}
	.socialMedia{
		flex-direction:column;
		height:350px;
	}
	.socialMediaIcon{
		flex-direction:column;
	}

}
.overlay{
	position:fixed;
	top:0;
	left:0;
	overflow: auto;
	width:100%;
	height:100%;
	background-color:rgba(0,0,0,0.5);
	z-index:4;
}

.showCartPopup{
	position:relative;
	margin: 15% auto;
	width: 80%;
	max-width:850px;
	background-color:#fff;
	border:10px solid black;
	text-align:center;
}

.showCartPopup h1{
	width:100%;
	padding:20px 0;
	margin-top:0;
	font-size:2.3em;
	background-color:#5CB0B9;
	text-align:center;

}

.cardInfoContainer{
	position:relative;
	display:flex;
	justify-content:flex-start;
	width:100%;
	/*height:70%;*/
	min-height:400px;
	/*border:2px solid black;*/
	padding:20px;
}

.noItemInCart h1{
	position:absolute;
	background-color:white;
	width:80%;
	top:50%;
	left:50%;
	-webkit-transform:translate(-50%,-50%);
	        transform:translate(-50%,-50%);
}

.itemInCartContainer{
	display:flex;

}

.itemInCart{
	width:45%;
	height:90%;
	border:2px solid #000;
}

.itemInCart img{
	width:100%;
	height:100%;
}

.itemInCartInfo{
	margin-left:10px;
}

.itemInCartInfo h2{
	font-size:1.8em;
	margin-bottom:20px;
}

.priceTable{
	width:90%;
	height:50%;
	margin-left:20px;
}

.priceTable td{
	font-size:1.4em;
	font-weight:600;
	text-align:left;

}

.closePopupBtn{
	position:absolute;
	top:-25px;
	right:-25px;
	width:50px;
	height:50px;
	font-size:2.5em;
	font-weight:700;
	border-radius:25px;
	border:2px solid #000;
	background-color:#fff;
}

.popupOkBtn{
	width:200px;
	min-height:50px;
	background-color:#0B7B9A;
	border:1px solid black;
	color:#fff;
	font-size:2.3em;
	margin:20px 0 40px 0;
}

@media screen and (max-width:800px){

	.showCartPopup{
		width:90%;
		min-width:320px;
	}

	.showCartPopup h1{
		font-size:1.5em;
		font-weight:700;
	}

	.cardInfoContainer{
		padding:0;		
	}


	.itemInCartContainer{
		flex-direction:column;
		margin:0 auto;
	}

	.itemInCartInfo h2{
		font-size:1.4em;
	}

	.itemInCart{
		width:100%;
		margin:20px 0;
		text-align:center;
	}

	.itemInCart img{
		width:250px;
		height:250px;
	}

	.priceTable{
		font-size:0.8em;
		margin:10px 20px 10px 20px;
	}
	.popupOkBtn{
		margin:10px 0 30px 0;
		width:80px;
		height:40px;
		padding:5px;
		font-size:1.5em;
	}

}
*{
  margin:0;
  padding:0;
  box-sizing:border-box;
}


